export const groupmanage = {
  path: '/groupmanage',
  name: 'groupmanage',
  title: '',
  component: () => import("../components/page/groupManage/index"),
  children: [
    {
      path: '/grouplist',
      name: 'grouplist',
      component: () => import("../components/page/groupManage/groupList"),
      meta: { title: '' },
      children: [
        {
          path: '/groupdetail',
          name: 'groupdetail',
          component: () => import("../components/page/groupManage/groupDetail"),
        }
      ]
    }
  ]
};



